<template>
  <div class="box">
    <van-steps 
    direction="vertical"
    active-color="#15BDDB"
    :active="active">
      <van-step>
        <h3 class="zhuti">提现申请已提交，等待处理中</h3>
        <p class="c9">账号：(123456789) </p>
        <p class="c9">时间：2016-07-12 12:40</p>
        <p class="c9">提现金额：9527.00</p>
      </van-step>
      <van-step>
        <h3>到账成功</h3>
      </van-step>
    </van-steps>
    <div class="fanhui">返回</div>
  </div>
</template>

<script>
import { kefu, gethelp } from '@/axios/getData'
import { Steps, Step } from 'vant';
export default {
  data(){
    return{
      data:[],
      active:1
    }
  },
  components: {
    vanSteps: Steps,
    vanStep: Step
  },
  created() {

  },
  methods:{

  }
}
</script>

<style scoped lang="scss">
.box{
  height:100vh;
  background: #fff;
  padding:40px;
  color:#999;
  .fanhui{
    width:70%;
    background: #15BDDB;
    color:#fff;
    padding:23px 0;
    font-size: 16px;
    margin:80px auto;
    text-align: center;
    border-radius: 60px;
  }
}
.zhuti{
  color:#15BDDB;
}
</style>



